
const NAME_TAXONOMY = {};
NAME_TAXONOMY['ID'] = ['GENERALID']
NAME_TAXONOMY['PHONE'] = ['BATTERY', 'DEVICE', 'NETWORK', 'RUNNINGSTATE', 'NOTIFICATION']
NAME_TAXONOMY['PERSONAL'] = ['CONTACTS', 'CALENDAR', 'SMS', 'STORAGE', 'ACCOUNT']
NAME_TAXONOMY['SENSOR'] = ['CAMERA', 'LOCATION', 'MICROPHONE', 'INERTIAL', 'PROXIMITY']
NAME_TAXONOMY['NONPRIVACY'] = ['NONPRIVACY']
// NAME_TAXONOMY['INSUFFICIENT']  = ['INSUFFICIENT']
// NAME_TAXONOMY['UNKNOWN']  = ['UNKNOWN']

const _taxonomies = [];
for (let prop in NAME_TAXONOMY) {
    const specifics = NAME_TAXONOMY[prop];
    if (specifics.length > 0) {
        const taxonomy = {
            id: prop,
            taxonomies : []
        };
        specifics.forEach(spec => {
            taxonomy.taxonomies.push(spec);
        });
        _taxonomies.push(taxonomy);
    }
}
export const TAXONOMIES = _taxonomies;

